import {uniPopup} from "@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue" 
export default {
	data() {  
		return {
		details:[],
		id:"",
		collection:1,
		}
	},
	onLoad:function(option){ 
		var _self = this
		_self.id = option.id
		console.log(_self.id)
		_self.getInfo()
	},
	methods: {
		previewImage: function(e) {
			uni.previewImage({
				current: e,
				urls: [e]
			})
		},
		getInfo:function(){
			var _self = this
			uni.request({ 
				url:_self.$api+"dockingManager/releaseMainQuery",
				data:{id:_self.id,pull:2,optionId:uni.getStorageSync("openId")}, 
				method:"GET",
				success:function(res){
					if(res.data[0].state === undefined || res.data[0].state===null){
						_self.collection = 1
					}else{
						_self.collection = 2
					}
					_self.details = res.data[0]
					console.log(res)
				}
			})
		},
			/**
			 * 转发
			 */
			onShareAppMessage: function (options) {
					var _self = this
					if (options.from === 'button') {
					  // 来自页面内转发按钮
					  console.log(options.target)
					}
					return {
					  //## 此为转发页面所显示的标题
					  //title: '好友代付', 
					  //## 此为转发页面的描述性文字
					  desc: _self.details.name, 
					  //## 此为转发给微信好友或微信群后，对方点击后进入的页面链接，可以根据自己的需求添加参数
					  path:  'pages/pageChildren/headerDetail/headerDetail?id='+_self.id+'&pull=6'+'&optionId='+uni.getStorageSync("openId"),
					  //## 转发操作成功后的回调函数，用于对发起者的提示语句或其他逻辑处理
					  success: function(res) {
						  uni.request({
						  url:_self.$api+"dockingManager/releaseHideAdd",
						  data:{id:_self.id,mark:1,optionId:uni.getStorageSync("openId")},
						  success:function(res){
							  console.log(res)
							  	console.log("转发成功")
						  },
						  }) 
					  },
					  //## 转发操作失败/取消 后的回调处理，一般是个提示语句即可
					  fail: function() {
						  console.log("error") 
					  }
					}
			},
			//拨打电话
			apply:function(){
				var _self = this
				 uni.makePhoneCall({
					phoneNumber: _self.details.phone //仅为示例
					});
			},
			/**
			 * 收藏
			 * 
			 */
			collect:function(){
				var _self = this
				if(this.collection == 1){
					uni.request({
						url:_self.$api+"dockingManager/releaseHideAdd",
						data:{id:_self.id,mark:0,optionId:uni.getStorageSync("openId")},
						success:function(res){
							console.log(res)
							if(res.data==1){
								console.log("收藏成功")
								_self.collection = 2
							}
							else if(res.data==99){
								uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
							}
							else if(res.data ==98){
									uni.showToast({
									title:"您尚未进行认证，请先进行认证",
									icon:"none"
								})
								return false
							}
							else{
								uni.showToast({
									title:"收藏失败",
									icon:"none"
								})
							} 
						}
					})
				}else{
						uni.request({
						url:_self.$api+"dockingManager/releaseHideDelete",
						data:{id:_self.id,optionId:uni.getStorageSync("openId")},
						success:function(res){
							console.log(res)
							if(res.data==1){
								console.log("取消收藏成功")
								_self.collection = 1
							}else if(res.data==99){
								uni.showModal({
									title: '提示',
									content: '此操作需用户授权，是否进行授权',
									success: function (res) {
										if (res.confirm) {
											//跳转到授权页面  
											uni.navigateTo({
												url:"../login/login"  
											})
											console.log('用户点击确定');
										} else if (res.cancel) {
											console.log('用户点击取消');
										}
									}
								});
							}
							else{
								uni.showToast({
									title:"取消收藏失败",
									icon:"none"
								})
							}						
						},
						})
				}
			},
	},
	 components: {uniPopup},
	
}
